html{scroll-behavior:smooth}body{margin:0;padding:0;box-sizing:border-box;-webkit-user-select:none;-ms-user-select:none;user-select:none;font-family:sans-serif;font-size:14px;background-color:#f7f8fa}.nav.nav-wrapper,#myFooter{background-color:red}.nav.nav-wrapper{font-family:'Aladin', cursive}.nav.nav-wrapper .brand-logo{font-size:230%}.nav.nav-wrapper li a{font-size:150%}nav ul li:hover{background-color:rgba(255,0,0,0.3)}ul.sidenav{background-color:#f7f8fa}#myWrapper{min-height:100vh;margin-bottom:-120px}@media screen and (max-width: 800px){#myWrapper{min-height:0;margin-bottom:60px;overflow:auto}}#myWrapper::after,#myFooter{content:"";display:block;height:120px;overflow:hidden}@media screen and (max-width: 800px){#myWrapper::after,#myFooter{display:none}}.sidenav-trigger{position:relative;top:10px;cursor:pointer}.sidenav-trigger>div{width:30px;height:3px;margin:7px;background-color:#fff;border-radius:30px}#myBackToTopButton{width:70px;height:70px;border-radius:50%;background-color:red;display:none;justify-content:center;align-items:center;border:7px solid #f7f8fa;color:#fff;font-size:200%;position:fixed;bottom:40px;right:40px}@media screen and (max-width: 800px){#myBackToTopButton{width:60px;height:60px;font-size:190%;bottom:70px;right:20px}}.input-field input[type=text]:focus+label,.input-field .materialize-textarea:focus+label{color:red !important}.input-field input[type=text]:focus,.input-field .materialize-textarea:focus{border-bottom:1px solid red !important;box-shadow:0 1px 0 0 red !important}.prefix.active{color:red !important}.tabs .tab a,.tabs .tab a:focus,.tabs .tab a:focus{background:#f7f8fa !important;color:red !important}.tabs .tab a.active,.tabs .tab a:focus,.tabs .tab a:focus.active{background:red !important;color:#fff !important}.tabs .indicator{background-color:#000 !important}[type="radio"]:checked+span:after,[type="radio"].with-gap:checked+span:before,[type="radio"].with-gap:checked+span:after{border:2px solid red !important}[type="radio"]:checked+span:after,[type="radio"].with-gap:checked+span:after{background-color:red !important}

#myFooterCopyright{font-size:90%;font-family:Dosis;color:#fff}#myDeveloper{font-family:Dosis}.mySocialIconsHolder{display:flex;justify-content:center;align-items:center}.mySocialIcon{display:flex;align-items:center;justify-content:center;text-decoration:none;font-size:160%;margin:5px;width:35px;height:35px;border-radius:30%;color:#3498db;background:#fff;overflow:hidden;position:relative;transition:0.2s linear}.mySocialIcon::before{content:"";display:block;width:120%;height:120%;transform:rotate(-45deg);background:#3498db;position:absolute;top:90%;left:-110%}.mySocialIcon:hover i{color:#fff;transform:scale(1.3)}.mySocialIcon:hover::before{-webkit-animation:aaa 0.7s 1;animation:aaa 0.7s 1;top:-10%;left:-10%}@-webkit-keyframes aaa{0%{top:90%;left:-110%}50%{top:-30%;left:10%}100%{top:-10%;left:-10%}}@keyframes aaa{0%{top:90%;left:-110%}50%{top:-30%;left:10%}100%{top:-10%;left:-10%}}

#myMobileFooterNav{margin:0;width:100%;height:56px;border-radius:20px 20px 0px 0px;background-color:red;position:fixed;bottom:0;box-shadow:0px 6px 12px rgba(0,0,0,0.2);display:none;justify-content:space-between;align-items:center}@media screen and (max-width: 800px){#myMobileFooterNav{display:flex}}#myMobileFooterNav div{margin:0px 10px;display:block}#myMobileFooterNav div .myFooterIcons a{margin:0px;font-family:Aladin, cursive;font-size:140%;color:#f7f8fa}



