
@import '../variables';


.myDefaultForm {

  h2{
    margin: 20px;
    font-family: 'Aladin', Dosis, sans-serif, cursive;
  }

  margin: 20px;
  margin-top: 50px;
  padding:30px;
  padding-top: 10px;

  
  
  box-shadow: $awesomeShadow;
  background-color: #fff;



  button {
    margin: 0;
    margin-top: 20px;
    background-color: $themeColor;
    &:hover {
      background-color: darken($themeColor, 10);
    }
  }

  #filterByFullTimeJobLabel {
    font-family: Aladin, sans-serif;
    font-size: 150%;
  }

}  



